<template>
    <div class="container">
        <div class="main__users" v-if="users">
            <div class="main__users-item users" v-for="user in users" :key="user.id">
                <router-link :to="/about/ + user.id" class="adress">Адресс</router-link>
                <p>Name: {{ user.name }}</p>
                <p>Email: {{ user.email }}</p>
                <p>City: {{ user.address.city }}</p>
            </div>
        </div>
        <div v-else class="Loading">
            Идет загрузка...
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            this.getUsers()
        },
        computed:{
            users() {
                return this.$store.state.user
            }
        },
        methods: {
            getUsers() {
                this.$store.dispatch('getUsers')
            }
        },
    }
</script>
